<script lang="ts">
import { defineComponent } from 'vue'
export default defineComponent({
  name: 'McMap',
})
</script>
<template>
    <div class="mc-map">
        <div class="mc-map__tools">
            <van-button class="mc-map__tools-btn calcel" @click="clickCancel"></van-button>
            <van-button class="mc-map__tools-btn submit" type="success" @click="clickSend"></van-button>
        </div>
        <div id="mChatMap">

        </div>
        <div class="mc-map__panel">
            <!-- <div class="mc-map__panel-item" v-for="(item, index) in state.infos" :key="index">
                {{ item.address }}
            </div> -->
            <div class="mc-map__panel-item">
                <div class="mc-map__address"> {{ state.location.address }}</div>
            </div>
        </div>
    </div>
</template>

<script lang="ts" setup name="McMap">
import AMapLoader from '@amap/amap-jsapi-loader';
import { Emitter } from 'mitt';
import { reactive, PropType, onMounted, inject } from 'vue';
import { Toast, Button as VanButton } from 'vant'
import * as eventsConst from '../utils/events.const';

// const props = defineProps({

// })
const state = reactive({
    location: {
        lonlat: [113.86697, 22.567978],
        address: '177 '
    },
    infos: []
})
const emitter = inject<Emitter<any>>('emitter');
let map;
let geocoder;


//
function onComplete(data) {
    console.log(':', data)
    // document.getElementById('status').innerHTML = ''
    // var str = [];
    // str.push('' + data.position);
    // str.push('' + data.location_type);
    // if (data.accuracy) {
    //     str.push('' + data.accuracy + ' ');
    // }//IP
    // str.push('' + (data.isConverted ? '' : ''));
    // document.getElementById('result').innerHTML = str.join('<br>');

    state.location.lonlat = [data.position.lng, data.position.lat];

    geocoder.getAddress(data.position, function (status, result) {
        if (status === 'complete' && result.regeocode) {
            var address = result.regeocode.formattedAddress;
            state.location.address = address;
        } else {
            console.error('')
        }
    });

}
//
function onError(data) {
    console.log(':', data)
    // document.getElementById('status').innerHTML = ''
    // document.getElementById('result').innerHTML = ':' + data.message + '</br>' + data.originMessage;
    Toast.fail('')
}

const clickSend = () => {
    emitter.emit(eventsConst.CLICK_MAP_SEND, state.location)
}
const clickCancel = () => {
    emitter.emit(eventsConst.CLICK_MAP_CANCEL)
}

onMounted(async () => {
    const AMap: any = await AMapLoader.load({
        "key": "599dfe6bbe92b35d34d4e3d3f40aac6b",              // WebKey load 
        "version": "2.0",   //  JSAPI  1.4.15
        "plugins": [
            'AMap.Geolocation'
        ],           // 'AMap.Scale'
        "AMapUI": {             //  AMapUI
            "version": '1.1',   // AMapUI 
            "plugins": ['misc/PositionPicker'],       //  AMapUI ui
        }
    })
    map = new AMap.Map('mChatMap');

    AMap.plugin("AMap.Geocoder", function () {
        geocoder = new AMap.Geocoder();
    })

    const geolocation = new AMap.Geolocation({
        enableHighAccuracy: true,//:true
        timeout: 10000,          //105s
        position: 'RB',    //
        offset: [10, 20], //[10, 20]
        zoomToAccuracy: true,   //

    });
    map.addControl(geolocation);
    geolocation.getCurrentPosition(function (status, result) {
        if (status == 'complete') {
            onComplete(result)
        } else {
            onError(result)
        }
    });

    return;
    AMapUI.loadUI(['misc/PositionPicker'], function (PositionPicker) {
        const positionPicker = new PositionPicker({
            mode: 'dragMap',
            map: map
        });

        positionPicker.on('success', function (positionResult) {
            // document.getElementById('lnglat').innerHTML = positionResult.position;
            // document.getElementById('address').innerHTML = positionResult.address;
            // document.getElementById('nearestJunction').innerHTML = positionResult.nearestJunction;
            // document.getElementById('nearestRoad').innerHTML = positionResult.nearestRoad;
            // document.getElementById('nearestPOI').innerHTML = positionResult.nearestPOI;
            state.infos = [{
                lnglat: positionResult.position,
                address: positionResult.address,
                nearestJunction: positionResult.nearestJunction,
                nearestRoad: positionResult.nearestRoad,
                nearestPOI: positionResult.nearestPOI
            }]
        });

        positionPicker.start();
    })


})
</script>
<style lang="scss">
.mc-map {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10;


    &__tools {
        position: absolute;
        top: 20px;
        left: 30px;
        right: 30px;
        display: flex;
        justify-content: space-between;
        z-index: 11;

        &-btn {
            width: 80px;
            height: 30px;
        }
    }

    &__panel {
        height: 20%;
        background-color: #fff;
        padding: 20px;
        box-sizing: border-box;

        overflow-y: auto;

        &-item {
            padding: 10px 0px;
            display: flex;
            align-items: center;
            border-bottom: 1px solid #f5f5f5;
        }
    }

    &__address {
        font-size: 4vw;
    }
}

#mChatMap {
    height: calc(100% - 20%);
    background-color: #fff;
    z-index: 10;
}
</style>